@import './mixin/font-size';
@import './mixin/z-index';
@import './mixin/media-query';
@import './globals/variables';
@import './header';
@import './base';
@import './home';
.map-section {
    width: 100%;
    text-align: center;
    h2 {
        @include font-size(35);
        margin-bottom: 1rem;
        font-weight: 500;
    }
    p {
        @include font-size();
        margin-bottom: 5rem;
        color: $grey-font-color;
    }
}

.sponsers-icons-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 85%;
    img {
        display: block;
        &:hover {
            cursor: pointer;
        }
    }
}

footer {
    width: clamp(1060px, 80%, 1400px);
    margin: 0 auto;
    background-color: $grey-background-color;
    .footer-container {
        width: 85%;
        margin: 0 auto;
        padding: 3rem 0;
        background-color: $grey-background-color;
    }
}

.footer__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .footer__logo-description {
        @include font-size(20);
        font-weight: 500;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 150px;
    }
    .footer__social-icons {
        
    }
}